import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { HttpService } from '@gsp-sys/rtf-common';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { MOCKPROCESSLIST } from './assets/mock';

@Injectable()
export class StartFlowService {
    constructor(private http: HttpService) {
        if (!environment.production) {
            const host = 'http://localhost:5000';
        }
    }

    fetchFlowList(): Observable<any> {
        return of(MOCKPROCESSLIST);
    }

    fetchAssignFlow(key: string, firstDe: string, secondDe: string): Observable<any> {
        const url = `/api/runtime/wf/v1.0/ProcDefs/AssignedProcDefs(bizDefKey='${key}',dimension1='${firstDe}',dimension2='${secondDe}')`;
        return this.http.get(url);
    }

    fetchInputVariables(proDefId: string): Observable<any> {
        const url = `/api/runtime/wf/v1.0/ProcDefs/InputVariables(procDefId='${proDefId}')`;
        return this.http.get(url);
    }
    /**
     * 启动流程
     * @param procDefId procDefId
     * @param procInsName procInsName
     * @param bizID bizId
     * @param description description
     * @param params params
     */
    startWorkflow(procDefId: string, procInsName: string, bizID: string, description: string, params: string[]): Observable<any> {
        const url = '/api/runtime/wf/v1.0/StartProcess';
        const options = {
            startContext: {
                ProcessDefId: procDefId,
                ProcessInstanceName: procInsName,
                Description: description,
                BizInstId: bizID,
                LastProcessInstanceId: '',
                Params: params
            }
        };

        return this.http.post(url, options);
    }
}



