import { Component, OnInit, ViewChild } from '@angular/core';
import { GridDataResult, SelectionEvent, SelectableSettings } from '@progress/kendo-angular-grid';
import { StartFlowService } from '../start-flow.service';
import { Process } from '../model/processDefinition';
import { StartFlowComponent } from '../start-flow/start-flow.component';

@Component({
  selector: 'app-flow-list',
  templateUrl: './flow-list.component.html',
  styleUrls: ['./flow-list.component.css']
})
export class FlowListComponent implements OnInit {
  @ViewChild(StartFlowComponent)
  private startProcessDlg: StartFlowComponent;
  gridView: {};
  selectedItem: Process;
  public selectableSettings: SelectableSettings = {
    enabled: true,
    mode: 'multiple'
  };
  constructor(private startService: StartFlowService) { }
  ngOnInit() {
    this.getFlowList();
  }

  startFlow(): void {
    if (!this.selectedItem) {
      alert('请选择要启动的流程！');
      return;
    }
    this.startService.fetchAssignFlow(this.selectedItem.key, this.selectedItem.firstDimension, this.selectedItem.secondDimension)
      .subscribe((result) => {
        if (result.value.length < 1) {
          alert('没有能启动的流程定义，请先发布！');
        }
        this.startProcessDlg.show(result.value[0].Id);
      });
  }


  /**
   * 列表选择
   * @param event 选中事件
   */
  onSelectionChange(event: SelectionEvent): void {

    if (event.selected) {
      this.selectedItem = event.selectedRows[0].dataItem;
    } else {
      this.selectedItem = null;
    }
  }
  /**
   * 获取流程列表
   */
  getFlowList(): void {
    this.startService.fetchFlowList().subscribe((result) => {
      this.gridView = {
        data: result.value,
        total: result.value.length
      };
    });
  }
}



