import { Component, OnInit } from '@angular/core';
import { StartFlowService } from '../start-flow.service';

@Component({
  selector: 'app-start-flow',
  templateUrl: './start-flow.component.html',
  styleUrls: ['./start-flow.component.css']
})
export class StartFlowComponent implements OnInit {
  private description: string;
  private bizID: string = this.guid();
  private procInsName: string;
  private procDefKey: string;
  private procInsSchema: any;
  private procDefId: string;
  active = false;
  constructor(private startService: StartFlowService) { }

  ngOnInit() {
  }

  private updateProcInsName(value: string) {
    this.procInsName = value;
  }
  private updateDescription(value: string) {
    this.description = value;
  }
  private updateBizId(value: string) {
    this.bizID = value;
  }
  guid() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
      // tslint:disable-next-line:no-bitwise
      const r = Math.random() * 16 | 0, v = c === 'x' ? r : (r & 0x3 | 0x8); return v.toString(16);
    });
  }


  show(procDefId: string): void {
    if (this.active) {
      return;
    }
    this.active = true;
    this.procDefId = procDefId;
    this.startService.fetchInputVariables(this.procDefId).subscribe((re) => {
      if (re) {
        // TODO 后续要支持传参的提交审批
      }
    });

  }

  cancel(): void {
    this.active = false;
  }
  // 提交审批
  submit(): void {
    this.startService.startWorkflow(this.procDefId, this.procInsName, this.bizID, this.description, [])
      .subscribe((re) => {
        this.active = false;
        alert('启动成功！');
      });
  }


}



