import { Routes, RouterModule } from '@angular/router';
import { FlowListComponent  } from './flow-list/flow-list.component';

const routes: Routes = [
    { path: '', component: FlowListComponent },
];

export const TaskStartFlowRoots = RouterModule.forChild(routes);



